@use 'base/variables' as v;
@use 'base/mixins' as m;

.anuncio {
    background-image: url(../img/chef_bg.jpg);
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
    &__contenido {
        padding-block: 20rem;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        gap: 3rem;
        max-width: 50rem;
    }
    &__heading {
        margin: 0;
        color: v.$blanco;
        text-align: center;
        font-weight: 700;
    }
    &__texto {
        margin: 0;
        color: v.$blanco;
        font-size: 2.6rem;
        line-height: 2;
        text-align: center;
    }
    &__boton {
        @include m.boton(v.$primario, v.$blanco);
    }
}